.container {
    width: 607px;
    height: 750px;
    margin: auto;
    margin-top: 75px;
    border-radius: 12px;
    background-image: linear-gradient(180deg, #130754 0%, #3b2f80 100%);
}

.top-bar {
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 55px;
}

.top-bar input {
    display: flex;
    width: 355px;
    height: 54px;
    background: #ebfffc;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: #626262;
    font-size: 20px;
    font-weight: 400;

}

.search-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 58px;
    height: 58px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;
}

.weather-image {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}

.weather-image img {
    height: 120px;
}

.weather-temp {
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400;
}

.weather-location {
    display: flex;
    justify-content: center;
    color: white;
    font-weight: 400;
    font-size: 50px;
    margin-bottom: 30px;
}

.data-container {
    margin-bottom: 50px;
    color: white;
    display: flex;
    justify-content: center;

}

.element {
    margin: auto;
    align-items: flex-start;
    display: flex;
    gap: 12px;
}

.data {
    font-size: 18px;
    font-weight: 400;

}

.text {
    font-size: 20px;
    font-weight: 400;
}

.icon {
    margin-top: 10px;
}